import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import wuye from '@/pages/index/index'// 
import Message from '@/pages/index/message'
import Pay from '@/pages/pay/pay'//
import PayStop from '@/pages/pay/pay-stop'//
import Paymentquery from '@/pages/paymentquery/paymentquery'//
import Suggest from '@/pages/suggest/suggest'//
import Myhouse from '@/pages/house/myhouse'//
import Addhouse from '@/pages/house/add-house'
import Bindhouse from '@/pages/house/bind-house'
import Mysteward from '@/pages/mysteward/index' // 
import threadDetail from '@/pages/mysteward/threadDetail'
import Maintain  from '@/pages/maintain/index'  //
import Register from '@/pages/register/register'// 

Vue.use(Router)

const router= new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component:Index,
      children:[
        {path:'',component:wuye},
   	  ]
    },
    {
      path:'',
      name:'wuye',
      component:wuye
    },
    {
      path:'/message',
      name:'message',
      component:Message,
      meta:{
        title:''
      }
    },
    {
      path:'/pay',
      name:'pay',
      component:Pay,
      meta:{
        title:''
      }
    },
    {
      path:'/pay-stop',
      name:'pay-stop',
      component:PayStop,
      meta:{
        title:''
      }
    },
    {
      path:'/paymentquery',
      name:'paymentquery',
      component:Paymentquery
    },
    {
      path:'/suggest',
      name:'suggest',
      component:Suggest
    },
    {
      path:'/myhouse',
      name:'myhouse',
      component:Myhouse,
      meta:{
        title:''
      }
    },
    {
      path:'/addhouse',
      name:'addhouse',
      component:Addhouse,
      meta:{
        title:''
      }
    },
    {
      path:'/bindhouse/:number',
      name:'bindhouse',
      component:Bindhouse,
      meta:{
        title:''
      }
    },
    {
      path:'/mysteward',
      name:'mysteward',
      component:Mysteward,
      meta:{
        title:''
      },    
    },
    {
      path:'/maintain',
      name:'maintain',
      component:Maintain,
      meta:{
        title:''
      }
    },
    {
      path:'/threadDetail',
      name:'threadDetail',
      component:threadDetail
    },
    {
      path:'/register',
      name:'/register',
      component:Register,
      meta:{
        title:''
      }
    }

  ]
})
router.beforeEach((to, from, next) => {
    //title
    changeTitle(to.meta.title)
    next();
});
//title
function changeTitle(title) {
    title = title ? title : '';
    window.document.title = title;
}
export default router


