<template>
   <div>
       <div class="main">
            <mt-loadmore 
			  	:bottomMethod="carLoadBottom" 
			  	:auto-fill = "false"
			  	:bottomAllLoaded = "carisLastPage"
				ref="loadmore"
			  >
			  	<Bill :bill-info="carBillInfo" @itemClick="itemClick"></Bill>
			</mt-loadmore>
            <div style="widtt:100%;height:0.92rem;"></div>
			  	<div class="btn-fixed">
		    		<div class="fl select-btn"  :class="{allSelected:carAllselect }" @click="allSelect(carBillInfo,'carAllselect')">&nbsp;</div>
		    		<div class="pay" @click="pay('carBillInfo','carAllPrice','carAllselect')">
		    			
		    			<span>
		    				{{carAllPrice}}
		    			</span>
		    		</div>
        		</div>
       </div>
   </div>
</template>

<script>
import wx from 'weixin-js-sdk';
import Bill from '../../components/bill.vue' 
let vm;
export default {
   data () {
       return {
           url : '/billList',
		   params : {
		  			startDate:'',
		  			endDate:'',
		  			payStatus: '02', //
		  			currentPage : 1, //
		  			totalCount : 0, //
		  		},
           carBillInfo:[],//
           carisLastPage:false,//
           reduceMode:1,//
           pay_least_month:0,//
           carBillPage:1,//
		   carAllselect:false,//
		   permit_skip_pay:'1'
       };
   },
   created() {
       vm=this;
   },
   mounted() {
        //
       	this.common.checkRegisterStatus();
        
        //
        vm.receiveData.getData(vm, vm.url, 'data',function(){
		  		vm.reduceMode = vm.data.result.reduce_mode;//
		  		vm.carBillInfo = vm.data.result.car_bill_info;//
				vm.pay_least_month = vm.data.result.pay_least_month; // 
				vm.permit_skip_pay=vm.data.result.permit_skip_pay;  
		},vm.params) ;
   },

   components: {Bill},

   methods: {
       //
       carLoadBottom() {
           //
		  		let tempArr = null;
		  		// 
		  		vm.carBillPage+=1
				vm.params.currentPage = vm.carBillPage;
				//
				vm.receiveData.getData(
					vm,
					vm.url,
					'pageData2',
					function(){
		  				tempArr = vm.pageData2.result.car_bill_info;//
		  				if(tempArr && tempArr.length > 0){
		  					vm.carBillInfo =vm.carBillInfo.concat(tempArr) //
		  					vm.carAllselect = false;
		  				}else{
							  vm.carisLastPage = true;
		  				}
		  			},vm.params)
		  		 vm.$refs.loadmore.onBottomLoaded()
       },
       // params1: params2:
       itemClick(index,b) {
           let len = b.length
		  	//permit_skip_pay  0  1
			if(vm.permit_skip_pay == '1') {
					if(b[index].selected){//
						for(let i=index;i< len;i++){
							//
							vm.$set(b[i],'selected',false);	
						}
						//
						vm.carAllselect = false;
					}else{//
						for(let j=index;j >= 0;j--){//
							vm.$set(b[j],'selected',true);	
						}
					}
			}else if(vm.permit_skip_pay == '0') {
				if(b[index].selected){//
					vm.$set(b[index],'selected',false);	
					//
					vm.carAllselect = false;
					//    console.log(vm.bAllSelect)
				}else{//
						vm.$set(b[index],'selected',true);	
					}
			}	
       },
       //
       allSelect(arr,a) {
           if(vm[a] ){//
		  			for(let i in arr){
		  				vm.$set(arr[i],'selected',false);
		  			}
		  		}else{//
		  			for(let i in arr){
		  				vm.$set(arr[i],'selected',true);
		  			}
		  		}
		  		//
		  	vm[a] = !vm[a];
       },
       //
       pay(list,allPrice,allselect){//    , string
            if( vm[allPrice] < 0.1){
		  			alert('');
		  			return
		  		}
		  		let selectedArr = [];
		  		if(vm[allselect] == true){
		  			//
		  			selectedArr = vm[list];
		  		}else{
		  			//
		  			for( let i in vm[list]){
		  				if(vm[list][i].selected){
		  					selectedArr.push(vm[list][i])
		  				}
		  			}
		  		}
		  		let bills="";
		  		let pay_addr = selectedArr[0].pay_addr;
		  		for(let i in selectedArr){
		  			if(selectedArr.length - 1 == i){
		  				bills+=selectedArr[i].bill_id;
		  			}else{
		  				bills+=selectedArr[i].bill_id+',';	
		  			}
		  			
		  		};
				if(vm.pay_least_month>0){
					if(vm.pay_least_month>selectedArr.length){
						alert(""+vm.pay_least_month+"");
						return false;
					}
                }
                
            let str = 'https://www.e-shequ.com/wangdu/weixin/pay/';
	  		let baseUrl='https://www.e-shequ.com/wangdu/weixin/';
	  		let url = str +"gmpaymentdetails.html?#/?billIds="+bills+"&stmtId="+vm.stmtId+"&payAddr="+escape(pay_addr)+"&totalPrice="+vm[allPrice]+"&reduceMode="+vm.reduceMode+"&basePageUrl="+baseUrl;
        	window.location.href = url  
       }
   },

   computed: {
       //
       carAllPrice() {
           let ap = 0;
		  		for(let i in this.carBillInfo){
		  			if(this.carBillInfo[i].selected == true){
		  				ap+=Number(this.carBillInfo[i].fee_price)
		  			}
		  		}
		  		return parseFloat(ap).toFixed(2)
       }
   },
}
</script>

<style  scoped>
a{color:black}
/**/

.query-data{
	height: 100%;
	padding: 0.25rem 1.2rem;
	margin-bottom: 0.2rem;
}
.virtual-input{
	width:3.4rem;
	border:1px solid #4c4c4c;
	border-radius: 5px;
	padding-left: 0.2rem;
	display: inline-block;
	height: 0.5rem;
}
.input-row{
	padding-bottom: 0.3rem;
	font: 0.26rem/0.5rem "";
	color: #a6937c;
	height: 0.5rem;
}
.top2{
	position: relative;
	top: -0.2rem;

}

.btn-fixed{
	position: fixed;
	color: #fff;
    left: 0;
    right: 0;
    bottom: 0;
    height: 0.92rem;
    line-height: 0.92rem;
    text-align: center;
}

.select-btn{
	padding-left: 36px;
    background: url('../../assets/wuye/icon_unselect_white.png') no-repeat;
    background-color: rgba(0,0,0,0.6);
    background-size: 16px;
    background-position: 15px center;
    height: 0.92rem;
    line-height: 0.92rem;
}

.allSelected{
	background: url('../../assets/wuye/icon_selected_white.png') no-repeat;
	 background-color: rgba(0,0,0,0.6);
    background-size: 16px;
    background-position: 15px center;

}

.pay{
	overflow: hidden;
	background: #D01120;
	text-align: center;	
}	

.main{
	margin:0 0.3rem;
}
</style>


